.banner {
  position: relative;
  background-image: url("../../images/cover.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vmin;
  text-align: center;
  color: white;
}

.banner > h1 {
  padding: 2% 0;
  font-weight: 900;
  font-size: 9vmax;
  color: rgb(0, 0, 0, 0.7);
  font-family: "Dancing Script", cursive;
}

.homeHeading {
  text-align: center;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-size: 1.4vmax;
  border-bottom: 1px solid rgba(21, 21, 21, 0.5);
  width: 20vmax;
  padding: 1vmax;
  margin: 5vmax auto;
  color: rgb(0, 0, 0, 0.7);
}

.container {
  display: flex;
  margin: 2vmax auto;
  width: 80vw;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 100%;
}

.productCard {
  width: 14rem;
  height: 24rem;
  text-decoration: none;
  color: rgb(0, 0, 0, 0.7);
  margin: 2vmax;
  transition: all 0.5s;
  box-sizing: border-box;
  padding: 3px;
}

.productCard-img-div {
  height: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.productCard-img-div > img {
  height: 100%;
  max-width: 100%;
}

.linksReactNavbar {
  font-family: "Poppins", sans-serif !important;
}

.productCard-details-div {
  width: 100%;
  height: 40%;
  padding: 2px 4px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-top: 5px;
}

.productCard-details-div > div {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: row;
}

.productCard-details-div > div > span {
  font-weight: 300;
  font-size: 16px;
  font-family: "Poppins", sans-serif;
  margin-left: 10px;
  margin-bottom: 10px;
}

.productCard-details-div > p {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  margin-bottom: 10px;
  font-weight: 900;
  white-space: nowrap;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
}

.productCard-details-div > span {
  color: tomato;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
}

.productCard:hover {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
  transition: all 0.5s;
  background: white;
}

@media screen and (max-width: 800px) {
  .logoReactNavbar {
    width: 100% !important;
  }
}
