.CartItemCard {
  display: flex;
  padding: 1vmax;
  height: 8vmax;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  box-sizing: border-box;
  width: 66%;
}

.cart-img-div {
  width: 20%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.cart-img-div > img {
  max-width: 100%;
  max-height: 100%;
}

.cart-item-details {
  width: 80%;
  display: flex;
  margin-left: 7px;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  box-sizing: border-box;
}

.cart-item-details > div > a {
  font: 300 1.4vmax "Poppins";
  color: rgba(0, 0, 0, 0.7);
  text-decoration: none;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.cart-item-name-div {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.cart-item-details > span {
  font: 300 0.9vmax "Poppins";
  color: rgba(0, 0, 0, 0.7);
}

.cart-item-details > p {
  color: tomato;
  font: 100 0.8vmax "Poppins";
  cursor: pointer;
}

@media screen and (max-width: 600px) {
  .CartItemCard {
    height: 25vmax;
    width: 50%;
  }

  .cart-img-div {
    width: 40%;
  }

  .cart-item-details {
    width: 60%;
  }

  .cart-item-details > div > a {
    font: 300 2vmax "Poppins";
  }

  .cart-item-details > span {
    font: 300 1.9vmax "Poppins";
  }

  .cart-item-details > p {
    font: 100 1.8vmax "Poppins";
  }
}
